<template>
  <div class="text-center">
    <v-dialog v-model="dialog" @input="$emit('cerrar')" width="500">
      <v-card>
        <v-card-title class="text-h5 titulo">
          {{ titulo }}
        </v-card-title>

        <v-card-text>
          <h3>
            {{ texto }}
          </h3>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="cerrar"> Aceptar </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialog: false,
    };
  },
  props: {
    //
    abrir: {
      abrir: Boolean,
      default: false,
    },
    texto: {
      texto: String,
      default: "",
    },
    titulo: {
      texto: String,
      default: "",
    },
  },
  watch: {
    abrir(val) {
      if (val) {
        this.dialog = val;
      }
    },
  },
  methods: {
    cerrar() {
      this.$emit("cerrar");
      this.dialog = false;
    },
  },
};
</script>

<style scoped>
.titulo {
  background-color: #393939;
  color: white;
}
</style>